import Vue from "vue";
import App from "./App.vue";
import router from "./router";
var VueScrollTo = require('vue-scrollto');
import axios from 'axios';
import VueAxios from 'vue-axios';

Vue.config.productionTip = false;
Vue.use(VueScrollTo);
Vue.use(VueAxios, axios);


new Vue({
  router,
  render: h => h(App)
}).$mount("#app");